<template>
  <div class="custom-edit-wrap">
    <el-row type="flex" class="row-bg" justify="space-between">
      <el-col :span="24">
        <el-row type="flex" class="row-bg" justify="space-between">
          <el-col :span="12">
            <h3>Web自定义规则配置</h3>
          </el-col>
          <el-col :span="12" class="text-align-right">
            <a
              class="el-button el-button--primary"
              :href="'/#/web-rule-protection/' + domain + '/' + sourceType"
              >返回</a
            >
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <div class="margin-4x"></div>
    <el-row v-loading.fullscreen.lock="loadingPage">
      <el-col :span="24">
        <el-form
          class="custom-edit-form"
          :model="webRuleProForm"
          :rules="rules"
          ref="webRuleProForm"
          label-width="180px"
        >
          <el-card class="box-card">     
            <div>
              <el-form-item label="规则名称" prop="rule_name">
                <el-input v-if="type == 'new'" v-model="webRuleProForm.rule_name" placeholder="请输入字母开头，字母或数字组合，仅支持_-两种符号"> </el-input>
                <el-input v-if="type == 'edit'" v-model="webRuleProForm.rule_name" disabled> </el-input>
              </el-form-item>
              <el-form-item label="规则详情">
                <el-input v-model="webRuleProForm.rule_detail"> </el-input>
              </el-form-item>
              <el-form-item label="规则依赖">
                <el-select v-model="depend" placeholder="请选择" multiple  >
                  <el-option
                    v-for="item in dependList"
                    :key="item.rule_uuid"
                    :label="item.rule_name"
                    :value="item.rule_uuid">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="匹配参数">
                <div class="match-box" v-for="(item, index) in ruleMatchs" :key="index">
                  <div class="match-box-content">
                    <div class="match_key_cascader">
                      <el-cascader 
                        separator=":"
                        v-model="item.rule_match_key_list"
                        :options="optionsMatchKey"
                        :props="propsMatchKey"
                        @change="onChangeRuleMatchs($event, item)"
                        clearable>
                      </el-cascader>
                    </div>
                    <div v-show="item.showInput" class="match_key_input">
                      <el-input v-model="item.rule_match_key" clearable @change="onChangeRuleInput($event, item)"></el-input>
                    </div>
                  </div>
                  
                  <el-button @click.prevent="removeRuleMatchs(item)">删除</el-button>
                </div>
                <el-button @click="addRuleMatchs" plain type="primary">新增</el-button>
              </el-form-item>
              <el-form-item label="匹配方式" prop="match_operator">
                <el-select v-model="webRuleProForm.match_operator" placeholder="请选择">
                  <el-option
                    v-for="item in optionsOperator"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="匹配内容" prop="match_value">
                <el-input v-model="webRuleProForm.match_value"></el-input>
              </el-form-item>
              <el-form-item label="参数预处理">
                <el-checkbox-group v-model="checkboxPreprocess" prop="checkboxPreprocess">
                  <el-checkbox label="lowerCase">小写处理</el-checkbox>
                  <el-checkbox label="base64Decode">BASE64解码</el-checkbox>
                  <el-checkbox label="length">长度计算</el-checkbox>
                  <el-checkbox label="uriDecode">URL解码</el-checkbox>
                  <el-checkbox label="uniDecode">UNICODE解码</el-checkbox>
                  <el-checkbox label="hexDecode">十六进制解码</el-checkbox>
                  <el-checkbox label="none">不处理</el-checkbox>
                </el-checkbox-group>
              </el-form-item>
              <el-form-item label="执行动作" prop="rule_action">
                <el-select v-model="webRuleProForm.rule_action" placeholder="请选择">
                  <el-option
                    v-for="item in ruleAction"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="规则优先级"  class="rule-level-box">
                <el-slider
                  class="match-inline-block"
                  v-model="ruleLevel"
                  :step="1"
                  :min="5"
                  :max="9"
                  :format-tooltip="formatTooltipLevel"
                  show-stops
                ></el-slider>
              </el-form-item>
              <el-form-item label="规则状态">
                <el-switch
                  v-model="webRuleProForm.status"
                  active-value="true"
                  inactive-value="false"
                ></el-switch>
              </el-form-item>
            </div>
          </el-card>
        </el-form>
        <div class="margin-4x"></div>
        <el-row type="flex" class="row-bg" justify="space-between">
          <el-col :span="24">
            <el-row type="flex" class="row-bg" justify="space-between">
              <el-col :span="12">
                <a
                  class="el-button el-button--default"
                  :href="'/#/web-rule-protection/' + domain + '/' + sourceType"
                  >返回</a
                >
              </el-col>
              <el-col :span="12" class="text-align-right">
                <el-button
                  type="primary"
                  @click="onClickWebRuleProSubmit('webRuleProForm')"
                  :loading="loading"
                  >保存</el-button
                >
              </el-col>
            </el-row>
          </el-col>
        </el-row>
      </el-col>
    </el-row>

  </div>
</template>
<script>
import { mixin, JXAjax, validateRuleName } from "../assets/scripts/common";
import { useRoute } from 'vue-router'
export default {
  mixins: [mixin],
  data() {
    return {
      loading: false,
      loadingPage: true,
      domain: '',
      sourceType: '',
      uuid:'new',
      webRuleProForm: {
        rule_detail: '',
        rule_depend_uuid:'',
        match_preprocess: '',
        match_key: []
      },
      type: 'edit',
      depend: [],
      dependList: [],
      optionsMatchKey: [
        {
          value: 'http',
          label: 'http',
          children: [
            { value: 'path', label: 'path' },
            { value: 'query_string', label: 'query_string' },
            { value: 'method', label: 'method' },
            { value: 'src_ip', label: 'src_ip' },
            { value: 'body', label: 'body' },
            { value: 'version', label: 'version' },
            { value: 'scheme', label: 'scheme' },
          ] 
        }, 
        {
          value: 'header',
          label: 'header',
          children: [
            { value: 'cookie', label: 'cookie' },
            { value: 'referer', label: 'referer' },
            { value: 'user_agent', label: 'user_agent' },
            { value: 'default', label: '自定义' },
          ] 
        }, 
        {
          value: 'cookie',
          label: 'cookie',
          children: [
            { value: 'default', label: '自定义' }
          ] 
        }, 
      ],
      propsMatchKey: { expandTrigger: 'hover' },
      ruleMatchs: [{
        rule_match_key_list: [],
        rule_match_key: '',
        showInput: false
      }],
      operator: '',
      optionsOperator: [
        { value: "rx", label: "正则匹配" },
        { value: "str_prefix", label: "前缀匹配" },
        { value: "str_suffix", label: "后缀匹配" },
        { value: "str_contain", label: "包含" },
        { value: "str_ncontain", label: "不包含" },
        { value: "str_eq", label: "等于" },
        { value: "str_neq", label: "不等于"},
        { value: "gt", label: "数字大于" },
        { value: "lt", label: "数字小于" },
        { value: "eq", label: "数字等于" },
        { value: "neq", label: "数字不等于" },
      ],
      ruleAction: [
        { value: "deny", label:"阻断" },
        { value: "redirect", label: "重定向到首页" },
        { value: "log", label: "观察模式" },
        { value: "pre_rule", label: "前置规则" },
        { value: "pre_rule_no_log", label: "前置规则(不记录日志)" },
      ],
      ruleLevel: 5,
      checkboxPreprocess: [],
    };
  },
  computed: {
    rules() {
      return {
        rule_name: [
          {
            required: true,
            message: '请输入规则名称',
            trigger: ["blur", "change"],
          },
          {
            validator: validateRuleName,
            trigger: ["blur", "change"],
          },
        ],
        match_operator: [
          { 
            required: true, message: '请选择匹配方式', trigger: 'change' 
          }
        ],
        match_value: [
          {
            required: true,
            message: '请输入匹配内容',
            trigger: ["blur", "change"],
          },
        ],
        rule_action: [
          {
            required: true,
            message: '请选择执行动作',
            trigger: 'change' 
          },
        ],
        checkboxPreprocess: [
          { type: 'array', required: true, message: '请至少选择一个', trigger: 'change' }
        ]
      }
    }
  },
  mounted() {
    var t = this;
    const route = useRoute()
    this.sourceType = route.params.sourceType;
    this.domain = route.params.domain;
    this.uuid = route.params.uuid;
    JXAjax(
      "post",
      "/waf/waf_get_rule_depend_list",
      { domain: t.domain, source_type: t.sourceType},
      function (response) {
        var dependList = response.data.message;
        
        for(var i in dependList) {
          if (dependList[i].rule_uuid != t.uuid) {
            t.dependList.push(dependList[i])
          }
        }

        if (t.uuid == 'new') {
          t.type = 'new'
          t.loadingPage = false
        } else {
          t.type = 'edit'
          t.loadingPage = true
          t.getData();
        }
      },
      function () {
        t.dependList = [];
      },
      "no-message"
    );

    
  },
  methods: {
    getData() {
      var t = this;
      JXAjax(
        "post",
        "/waf/waf_get_web_rule_protection",
        { domain: t.domain, source_type: t.sourceType, rule_uuid: t.uuid},
        function (response) {
          t.loadingPage = false;
          t.webRuleProForm = response.data.message;
          t.checkboxPreprocess = t.webRuleProForm.match_preprocess.split(",");
          var keyList = t.webRuleProForm.match_key.substr(1).slice(0, -1).split(",");
          t.ruleMatchs = [];
          keyList.forEach(element => {
            t.ruleMatchs.push(
              {
                rule_match_key: element,
                rule_match_key_list: element.split(":"),
                showInput: true
              }
            )
          });
          t.ruleLevel = Number(t.webRuleProForm.rule_level);
          var _uuidStr = t.webRuleProForm.rule_depend_uuid.replace(/\s*/g,"").replace(/u/g, "").replace(/'/g,"").replace("[", "").replace("]", "");
          t.depend = _uuidStr.split(",")
          console.log(t.depend)
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    onClickWebRuleProSubmit(webRuleProForm) {
      var t = this;
      var postUrl = "/waf/waf_edit_web_rule_protection";
      if (t.type == 'new') {
        postUrl = "/waf/waf_create_web_rule_protection"
      } else {
        t.webRuleProForm.rule_uuid = t.uuid;
      }
      t.webRuleProForm.domain = t.domain;
      t.webRuleProForm.source_type = t.sourceType;
      t.webRuleProForm.match_preprocess = t.checkboxPreprocess.join(",");
      t.webRuleProForm.rule_level = t.ruleLevel.toString();
      t.webRuleProForm.rule_depend_uuid = t.depend;
      var str = "{";
      for(var i in t.ruleMatchs) {
         str += t.ruleMatchs[i].rule_match_key + ","
      }
      t.webRuleProForm.match_key = str.slice(0, -1) + "}"
      this.$refs[webRuleProForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.webRuleProForm,
            function (response) {
              t.loading = false;
              if(t.type == 'new') {
                window.location.href = '/#/web-rule-protection/' + t.domain + '/' + t.sourceType
              } else {
                t.getData();
              }
              
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
    removeRuleMatchs(item) {
      var index = this.ruleMatchs.indexOf(item);
      if (index !== -1) {
        this.ruleMatchs.splice(index, 1);
      }
    },
    addRuleMatchs(){
      this.ruleMatchs.push({rule_match_key: '', showInput: false})
    },
    onChangeRuleMatchs(event, item) {
      var index = this.ruleMatchs.indexOf(item);
      if(event[1] == 'default') {
        this.ruleMatchs[index].showInput = true;
        this.ruleMatchs[index].rule_match_key = event[0] + ':';
      } else {
        this.ruleMatchs[index].rule_match_key = event[0] + ':' + event[1];
      }
    },
    onChangeRuleInput(event, item) {
      var index = this.ruleMatchs.indexOf(item);
      if(event == '') {
        this.ruleMatchs[index].showInput = false;
        this.ruleMatchs[index].rule_match_key = "";
        this.ruleMatchs[index].rule_match_key_list = [];
      } else {
        this.ruleMatchs[index].rule_match_key = event;
      }
    },
    formatTooltipLevel(val) {
      var arrLevel = {
        9: "最高",
        8: "高",
        7: "中",
        6: "低",
        5: "最低",
      };
      return arrLevel[val];
    },
  },
};
</script>
<style>
.custom-edit-wrap {
  max-width: 800px;
  min-width: 400px;
}
.custom-edit-wrap .match-inline-block {
  width: 192px;
}
.custom-edit-form .el-checkbox + .el-checkbox {
  margin-left: 0px;
  margin-right: 30px;
}
.custom-edit-form .el-checkbox {
  margin-left: 0px;
  margin-right: 30px;
}
.custom-edit-form .el-select {
  width: 100%;
}
.custom-edit-form .match-box {
  
  display: inline-block;
  margin-bottom: 10px;
}
.custom-edit-form .el-button {
  margin-left: 10px;
}

.custom-edit-form .match-box-content {
  position: relative;
  display: inline-block;
}
.custom-edit-form .match_key_cascader {
  position: relative;
  display: inline-block;
}
.custom-edit-form .match_key_input {
  position: absolute;
  display: inline-block;
  top: 0;
  left: 0;
  width: 100%;
}
.custom-edit-form .rule-level-box .el-form-item__content {
  margin-left: 10px;
}
</style>
